@import url(https://fonts.googleapis.com/css2?family=Vollkorn:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
body{
    -ms-scroll-chaining:none;
        overscroll-behavior:none;
    font-family:"Volkorn", "san-serif";
    margin:0;
}
.expand{
    min-height:100vh;
    margin:2em 0;
    width:99%;
    transition-duration:.3s;
}
#editor-wrapper{
    width:600px;
    max-width:100%;
    margin:4em 0;
    transition-duration: 0.3s;
}
textarea{
    width:99%;
    min-height:200px;
    background-color:#dbe2f0;
    border:1px solid black;
    box-shadow:1px 1px 10px 2px #172136;
    padding-left:5px;
    outline:none;
}
.toolbar{
    background-color:#4b70b4;
    width:100%;
    padding:5px 0;
    font-weight:900;
    font-family: "Russo One"; 
    box-shadow:1px 1px 10px 2px #172136;
    position:relative;
    border-top:1px solid black;
    border-right:1px solid black;
    border-left:1px solid black;
}
span{
    margin:0 5px 0 5px;
}

button{
    border:none;
    background-color:#4b70b4;
    float:right;
    outline:none;
    cursor:pointer;
}
button:hover{
    color:#edf1f7;
    transition-duration:1s;
}

#previewer-wrapper{
    width:800px;
    max-width:100%;
    margin-bottom:3em;
    transition-duration:.3s;
}
#previewer{
    background-color:#dbe2f0;
    border:1px solid black;
    width:98%;
    box-shadow:1px 1px 10px 2px #172136;
    padding:0 .5em 0 .5em;
}
#wrapper{
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background-color:#92A8D1;
  width:100%;
  margin:0;
}

h1{
  border-bottom:2px solid #172136;
}
h2{
  border-bottom:1px solid #172136;
}
pre{
  background-color:white;
  white-space:pre;
  padding:5px 0 5px 5px;
}
code{
  background-color:white;
  padding:1px 4px 2px 4px;
}
p{
  display:block;
}
table{
  border-collapse:collapse;
  text-indent:0;
  text-indent:initial;
}
tr{
  display:table-row;
}
th{
  border:2px solid#172136;
  display:table-cell;
  padding:2px 4px;
}
tbody{
  display:table-row-group;
}   
td{
  border:2px solid #172136;
  padding:2px 4px;
}
.expand{
  width:100%;
}
.hide{
  display:none;
}
blockquote{
  color:#172136;
  font-weight:700;
  border-left:3px solid #172136;
  padding-left:5px;
}
